import React from 'react';
import {BrowserRouter, Route, Switch} from 'react-router-dom';
import '../style/index.css';
import {
  DynamicScreen,
  MarketScreen,
  PolicyScreen,
  TradeScreen,
  UserScreen,
  LoginScreen,
} from '../screens/index';
import TabBar from './TabBar';

class HomeScreen extends React.PureComponent {
  render() {
    return (
      <div style={{display: 'flex', flex: 1}}>
        <Switch>
          <Route path="/home/dynamic" component={DynamicScreen}/>
          <Route path='/home/market' component={MarketScreen}/>
          <Route path='/home/policy' component={PolicyScreen}/>
          <Route path='/home/trade' component={TradeScreen}/>
          <Route path='/home/user' component={UserScreen}/>
        </Switch>
        <TabBar/>
      </div>
    );
  }
}

class App extends React.PureComponent {
  render() {
    console.log(this.props)
    return (
      <BrowserRouter>
        <Route path='/home' component={HomeScreen}/>
        <Route path="/login" component={LoginScreen}/>
      </BrowserRouter>
    );
  }
}

export default App;
